<template>
  <div class="register-view">
    <template v-if="isComplete">
      <h1 class="title">注册成功</h1>
      <form method="post" onsubmit="return false">
        <p class="tip">请复制以下Token进行登录</p>
        <div class="form-alias">
          <label>
            <strong>token</strong>
            <input
              v-model="token"
              type="text"
              name="token"
              placeholder="token">
          </label>
        </div>
        <div class="form-submit">
          <router-link class="submit" :to="{ name: 'LoginView'}" tag="button">
            去登录
          </router-link>
        </div>
      </form>
    </template>
    <template v-else>
      <h1 class="title">欢迎加入豆瓣</h1>
      <form method="post" @submit.prevent="onSubmit()">
        <p v-if="error" class="tip error">{{error}}</p>
        <div class="form-alias">
          <label>
            <strong>邮箱</strong>
            <input
              v-model.trim="email"
              type="text"
              name="email"
              placeholder="邮箱">
          </label>
        </div>
        <div class="form-pwd">
          <label>
            <strong>请输入密码</strong>
            <template v-if="passType === 'password'">
              <input
                v-model.trim="pass"
                name="pass"
                type="password"
                placeholder="密码">
            </template>
            <template v-if="passType === 'text'">
              <input
                v-model.trim="pass"
                name="pass"
                type="text"
                placeholder="密码">
            </template>
            <span class="show-pwd" :class="{show: isShow}" @click="showPwd()"></span>
          </label>
        </div>
        <div class="form-name">
          <label>
            <strong>用户名</strong>
            <input
              v-model="name"
              name="name"
              type="text"
              placeholder="用户名">
          </label>
        </div>
        <div class="form-submit">
          <button
            type="submit"
            class="submit"
            :disabled="isDisabled"
            :class="{disabled: isDisabled}">
            {{registerState}}
          </button>
        </div>
      </form>
      <div class="footer">
        <div class="agreement">点击「注册」代表你已阅读并同意用户使用协议</div>
        <div class="btns">
          <a href="#">下载豆瓣App</a>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'register-view',
    data () {
      return {
        isShow: 0,
        isComplete: false,
        isDisabled: false,
        registerState: '立即注册',
        error: '',
        passType: 'password',
        pass: '',
        name: '',
        token: ''
      }
    },
    methods: {
      showPwd () {
        this.isShow = this.isShow ? 0 : 1
        this.isShow ? this.passType = 'text' : this.passType = 'password'
      },
      beforeSubmit () {
        this.isDisabled = true
        this.registerState = '正在提交...'
      },
      onSuccess () {
        this.isComplete = true
        this.token = res.body.token
      },
      onError () {
        this.error = err.body.error
        this.registerState = '立即注册'
        this.isDisabled = false
      },
      onSubmit () {
        this.beforeSubmit()
        this.$store.dispatch({
          type: 'register',
          email: this.email,
          pas: this.pass,
          name: this.name
        }).then(res => {
          this.onSuccess(res)
        }, err => {
          this.onError(err)
        })
      },
      beforeRouterEnter (to, from, next) {
        next(vm => {
          if (vm.$store.getters.currentUser.email) {
            vm.$router.push({name: 'StatusView'})
          } else {
            next()
          }
        })
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .register-view {
    h1 {
      margin: 10% 0 9%;
      font-size: 4rem;
      font-weight: 300;
      color: #42bd56;
      text-align: center;
    }

    form {
      padding: 2rem 1.5rem;

      strong {
        font-size: 1.5rem;
        color: #222;
        display: none;
        margin-bottom: 0.5rem;
      }

      input::placeholder {
        color: #ccc;
      }

      input[type="text"], input[type="password"] {
        display: inline-block;
        width: 100%;
        height: 4.4rem;
        padding: 0 0.8rem;
        box-sizing: border-box;
        font-size: 1.5rem;
        background: #fff;
        border: 0.1rem solid #ccc;
        border-top-left-radius: 0.3rem;
        border-bottom-right-radius: 0.3rem;
        outline: 0;
      }

      .form-pwd input, .form-name input {
        padding-right: 4rem;
        border-top: 0;
      }

      .form-pwd {
        position: relative;

        .show-pwd {
          position: absolute;
          right: 0.2rem;
          top: 0;
          display: block;
          height: 100%;
          width: 3.2rem;
          background-image: url(data:image/png;base64,);
          background-repeat: no-repeat;
          background-position: center center;
          background-size: 2.2rem;
          z-index: 3;
        }

        .show-pwd.show {
          background-image: url(data:image/png;base64,);
        }
      }

      .submit {
        cursor: pointer;
        width: 100%;
        padding: 1.2rem 2.6rem;
        margin-top: 1rem;
        font-size: 1.7rem;
        text-align: center;
        color: #fff;
        background: #42bd56;
        border: 0.1rem solid #17AA52;
        border-radius: 0.3rem;
      }

      .disabled {
        cursor: not-allowed;
        background: #eee;
        border: none;
      }

      .tip {
        font-size: 1.4rem;
        color: #aaa;
      }

      .error {

      }
    }

    .footer {
      .agreement {
        font-size: 1.4rem;
        color: #aaa;
        text-align: center;
      }

      .btns {
        margin-top: 4rem;
        text-align: center;
        font-size: 1.5rem;

        a {
          color: #42bd56;
          margin-right: 1.5rem;
        }
      }
    }
  }
</style>



